
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Navigation-bar {
  padding: 15px;
  text-align: left;
  overflow: hidden;
  background-color: #333;
}

.Navigation-bar a {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: calc(10px + 1vmin);
}

.Navigation-bar a:hover {
  background-color: #ddd;
  color: black;
}

div.sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

h1, h2, h3, h4 {
  margin: 0;
  padding: 10px;
}
